import React, { useState, useEffect } from 'react';

const QuizSection = () => {
  const [answers, setAnswers] = useState({
    question1: true,
    question2: true,
    question3: true,
    question4: true,
    question5: true,
    question6: true,
    question7: true
  });

  const [displayedPercentage, setDisplayedPercentage] = useState(100);

  const questions = [
    {
      id: 'question1',
      text: 'Do you own your domain, hosting, and have access to your cloud provider?',
      risk: "If you don't, you're not in control. You can be locked out, ransomed, or completely paralyzed if the relationship with the agency sours."
    },
    {
      id: 'question2',
      text: 'Do you own all your repositories and API documentation?',
      risk: "Without ownership and documentation, you can't audit the code, fix bugs, or onboard new developers — you're stuck with whoever wrote it."
    },
    {
      id: 'question3',
      text: 'Do you have a plan B to make a fix and deploy to production without the help of your agency?',
      risk: "Things break. If you can't fix and deploy without your agency, your business is at their mercy during emergencies."
    },
    {
      id: 'question4',
      text: 'If your agency disappeared tomorrow, could you keep the product running and continue building?',
      risk: "This reveals your actual level of independence. If the answer is no, you don't own a product — you rent one."
    },
    {
      id: 'question5',
      text: 'Is the person who truly understands how your product works — end to end — on your side?',
      risk: "If no one on your team understands the tech, you're not making informed decisions — you're just following orders and hoping it works."
    },
    {
      id: 'question6',
      text: "Do you have signed CLAs of everyone who's ever written a line of code?",
      risk: "Without legal agreements, you may not actually own your code — and that's a nightmare for fundraising, acquisitions, or legal disputes."
    },
    {
      id: 'question7',
      text: 'Did you know how much your last epic was going to cost before development?',
      risk: "Building without cost clarity leads to budget blowups, scope creep, and wasted money — and it's usually a sign of poor planning or a bad partner."
    }
  ];

  const handleAnswerChange = (questionId, currentValue) => {
    setAnswers(prev => ({
      ...prev,
      [questionId]: !currentValue
    }));
  };

  const calculateScore = () => {
    const yesCount = Object.values(answers).filter(answer => answer === true).length;
    return Math.round((yesCount / 7) * 100);
  };

  // Animation effect for percentage counter
  useEffect(() => {
    const targetPercentage = calculateScore();

    if (displayedPercentage !== targetPercentage) {
      const increment = displayedPercentage < targetPercentage ? 1 : -1;
      const timer = setInterval(() => {
        setDisplayedPercentage(prev => {
          const next = prev + increment;
          if ((increment > 0 && next >= targetPercentage) || (increment < 0 && next <= targetPercentage)) {
            clearInterval(timer);
            return targetPercentage;
          }
          return next;
        });
      }, 20); // Fast animation - 20ms intervals

      return () => clearInterval(timer);
    }
  }, [answers, displayedPercentage]);

  const getScoreMessage = (score) => {
    if (score === 100) {
      return "Excellent! You're well-prepared for technical leadership.";
    } else if (score >= 71) {
      return "Good foundation, but there are gaps that could cost you.";
    } else if (score >= 43) {
      return "Some technical leadership skills, but significant risks remain.";
    } else {
      return "Critical gaps in technical leadership - immediate attention needed.";
    }
  };

  const score = calculateScore();

  return (
    <section className="bg-white py-20 px-4" id="technical-readiness-quiz">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-apple-gray-800 mb-6 leading-tight">
            If You Can't Answer "Yes" to All These Questions, You Need Help
          </h2>
          <p className="text-lg text-apple-gray-600 max-w-2xl mx-auto">
            Take this quick assessment to identify critical gaps in your technical leadership readiness.
          </p>
        </div>

        {/* Quiz Questions */}
        <div className="max-w-3xl mx-auto mb-8">
          <div className="space-y-6">
            {questions.map((question, index) => (
              <div key={question.id} className="bg-apple-gray-50 p-6 rounded-lg border border-apple-gray-200">
                <div className="flex items-start space-x-4">
                  <div className="flex-shrink-0 w-8 h-8 bg-apple-gray-800 text-white rounded-full flex items-center justify-center text-sm font-semibold">
                    {index + 1}
                  </div>
                  <div className="flex-grow">
                    <p className="text-apple-gray-800 font-medium mb-4 leading-relaxed">
                      {question.text}
                    </p>
                    <div className="flex items-center space-x-4 mb-4">
                      <span className="text-sm font-medium text-apple-gray-600">No</span>
                      <button
                        onClick={() => handleAnswerChange(question.id, answers[question.id])}
                        className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none focus:ring-2 focus:ring-apple-gray-500 focus:ring-offset-2 ${answers[question.id] === true ? 'bg-apple-gray-800' : 'bg-apple-gray-300'
                          }`}
                      >
                        <span
                          className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${answers[question.id] === true ? 'translate-x-6' : 'translate-x-1'
                            }`}
                        />
                      </button>
                      <span className="text-sm font-medium text-apple-gray-600">Yes</span>
                    </div>

                    {/* Risk Warning - appears when answer is No */}
                    <div
                      className={`transition-all duration-300 ease-in-out ${answers[question.id] === false
                        ? 'opacity-100 max-h-32 transform translate-y-0'
                        : 'opacity-0 max-h-0 transform -translate-y-2 overflow-hidden'
                        }`}
                    >
                      <div className="bg-apple-gray-50 border-l-4 border-red-400 p-3 rounded-r-lg">
                        <div className="flex items-start">
                          <div className="flex-shrink-0">
                            <svg className="h-4 w-4 text-apple-gray-600 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                            </svg>
                          </div>
                          <div className="ml-2">
                            <p className="text-sm text-apple-gray-800 font-medium">Risk:</p>
                            <p className="text-sm text-apple-gray-600 mt-1">{question.risk}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Score Display */}
        <div className="max-w-2xl mx-auto">
          <div className="bg-apple-gray-100 p-8 rounded-lg text-center">
            <div className="mb-4">
              <div className="text-4xl font-bold text-apple-gray-800 mb-2">
                {displayedPercentage}%
              </div>
            </div>

            <div className="mb-6">
              <p className="text-lg font-medium text-apple-gray-800 mb-4">
                {getScoreMessage(score)}
              </p>

              <div
                className={`bg-white p-6 rounded-lg border border-apple-gray-200 transition-all duration-500 ease-in-out ${score < 100
                  ? 'opacity-100 max-h-96 transform translate-y-0'
                  : 'opacity-0 max-h-0 transform -translate-y-4 overflow-hidden'
                  }`}
              >
                <div className="flex items-center justify-center mb-4">
                  <div className="bg-apple-gray-800 p-3 rounded-lg mr-4">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                    </svg>
                  </div>
                  <h4 className="text-lg font-semibold text-apple-gray-800">Don't Let Technical Gaps Sink Your Startup</h4>
                </div>
                <p className="text-apple-gray-600 mb-6">
                  Every "No" represents a potential 6-7 figure mistake waiting to happen. Get the technical guidance you need before it's too late.
                </p>
                <a
                  href="https://form.typeform.com/to/SLoI0dkA"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-block bg-apple-gray-800 text-white font-semibold py-3 px-6 rounded-lg hover:bg-apple-gray-700 transition-colors duration-200"
                >
                  Get Technical Leadership Help
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default QuizSection;