import React, { useState, useEffect } from 'react';

const TypingAnimation = () => {
  const phrases = [
    "Don't Let Your Agency Burn Your Budget.",
    "Control Where Your Cash is Actually Going.",
    "Clarity for Founders Lost in Dev Talk.",
    "Stop Guessing. Start Leading Your Tech Team.",
    "If You're the Last to Know, That's No Bueno."
  ];

  const [currentPhraseIndex, setCurrentPhraseIndex] = useState(0);
  const [displayedText, setDisplayedText] = useState('');
  const [isTyping, setIsTyping] = useState(true);

  useEffect(() => {
    const currentPhrase = phrases[currentPhraseIndex];

    if (isTyping) {
      if (displayedText.length < currentPhrase.length) {
        const timeout = setTimeout(() => {
          setDisplayedText(currentPhrase.slice(0, displayedText.length + 1));
        }, 35); // Typing speed
        return () => clearTimeout(timeout);
      } else {
        // Finished typing, wait 3 seconds then start erasing
        const timeout = setTimeout(() => {
          setIsTyping(false);
        }, 3000);
        return () => clearTimeout(timeout);
      }
    } else {
      // Delete the whole phrase at once and move to next phrase
      setDisplayedText('');
      setCurrentPhraseIndex((prev) => (prev + 1) % phrases.length);
      setIsTyping(true);
    }
  }, [displayedText, isTyping, currentPhraseIndex, phrases]);

  return (
    <span>
      {displayedText}
      <span className="animate-pulse">|</span>
    </span>
  );
};

const HeroSection = () => {
  return (
    <section className="bg-white py-20 px-4 mt-16" id="home">
      <div className="max-w-2xl mx-auto text-center">
        {/* Illustration */}
        <div className="mb-10">
          <img
            src="/chaostoorder.png"
            alt="Chaos to Order"
            className="w-full max-w-sm mx-auto h-auto"
          />
        </div>

        {/* Headline */}
        <div className="h-24 md:h-32 flex items-center justify-center mb-4">
          <h1 className="text-4xl md:text-5xl font-bold text-apple-gray-800 leading-tight text-center">
            <TypingAnimation />
          </h1>
        </div>

        {/* Subheadline */}
        <p className="text-lg md:text-xl text-apple-gray-600 mb-8 leading-relaxed">
          Helping non-technical founders work confidently with their dev teams without getting lost, stuck, blindsided, or going broke in the process.
        </p>

        {/* Call to Action Button */}
        <div className="mb-4">
          <a
            href="https://form.typeform.com/to/SLoI0dkA"
            target="_blank"
            rel="noopener noreferrer"
            className="inline-block bg-apple-gray-800 text-white font-semibold py-4 px-8 rounded-lg hover:bg-apple-gray-700 transition-colors duration-200"
          >
            Book Your Assessment Call
          </a>
        </div>

        {/* Supporting Line */}
        <p className="text-sm text-apple-gray-400">
          Free 30-min audit. No pitch. Just clarity.
        </p>
      </div>
    </section>
  );
};

export default HeroSection;